"use client"

import { Container } from "@/components/container"
import { SectionHeading } from "@/components/section-heading"
import { testimonials } from "@/lib/data"
import { Card, CardContent } from "@/components/ui/card"
import { Star, Quote } from "lucide-react"
import { useState } from "react"

export function Testimonials() {
  const [activeIndex, setActiveIndex] = useState(0)

  return (
    <section className="py-20 bg-background">
      <Container>
        <SectionHeading
          title="What Our Community Says"
          subtitle="Hear from parents, students, and alumni about their experiences at Canary Academy."
        />

        <div className="mt-12 max-w-4xl mx-auto">
          <Card className="border-2 border-[#F5A623]/20 shadow-premium">
            <CardContent className="p-8 md:p-12">
              <Quote className="w-12 h-12 text-[#F5A623] mb-6" />

              <div className="space-y-6">
                <p className="text-lg md:text-xl text-foreground leading-relaxed italic">
                  {testimonials[activeIndex].content}
                </p>

                <div className="flex items-center gap-1">
                  {Array.from({ length: testimonials[activeIndex].rating }).map((_, i) => (
                    <Star key={i} className="w-5 h-5 fill-[#F5A623] text-[#F5A623]" />
                  ))}
                </div>

                <div>
                  <div className="font-bold text-[#2C4F5E] text-lg">{testimonials[activeIndex].name}</div>
                  <div className="text-muted-foreground">{testimonials[activeIndex].role}</div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Navigation Dots */}
          <div className="flex items-center justify-center gap-2 mt-8">
            {testimonials.map((_, index) => (
              <button
                key={index}
                onClick={() => setActiveIndex(index)}
                className={`w-3 h-3 rounded-full transition-all duration-300 ${
                  index === activeIndex ? "bg-[#F5A623] w-8" : "bg-border hover:bg-[#F5A623]/50"
                }`}
                aria-label={`View testimonial ${index + 1}`}
              />
            ))}
          </div>
        </div>
      </Container>
    </section>
  )
}
